/*-----------------------------------------------------------------------------------------------------
    [utilities:accessibility]
-------------------------------------------------------------------------------------------------------*/
.u-sr-only {
  clip: rect(0 0 0 0);
  clip: path(inset 100%);
  height: 1px;
  width: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
}

.u-show-on-focus {
  position: absolute;
  top: -100%;
  z-index: 1000;
}

.u-show-on-focus:focus {
  top: 0;
  background-color: #222;
  color: #FFF;
}

.u-visually-hidden {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap;
}

